export type Session = { role: 'parent' | 'agency'; agencyId?: string; email: string };

export async function login(input: { email: string; password: string }): Promise<Session> {
  if ((input.email === 'admin' || input.email === 'admin@example.com') && input.password === 'password123') {
    return { role: 'parent', email: 'admin' };
  }
  return { role: 'agency', email: input.email, agencyId: 'agency-001' };
}


